import React, { Component } from 'react'
import JoyStick from './Joystick'

class Canvas extends Component{
    state = {
        handleChange: this.props.onChange,
        canvasName: this.props.name,
        joystick: null
    }

    componentDidMount(){

        const canvas = document.getElementById(this.state.canvasName)

        canvas.width = canvas.getBoundingClientRect().width
        canvas.height = canvas.getBoundingClientRect().height

        console.log(canvas.getBoundingClientRect());

        let position ={
            x: canvas.width/2,
            y: canvas.height/2
        }

        this.setState({
            joystick: new JoyStick(canvas, position, 80, this.state.handleChange)
        })
    }

    componentWillUnmount(){

        this.state.joystick.cleanUp()
        this.setState({
            joystick: null
        })
    }

    render(){
        return (
            <canvas id={this.state.canvasName}></canvas>
        )
    }
}


export default Canvas