import React, {Fragment, useEffect} from 'react'
import NavBar from '../Components/NavBar'
import Footer from '../Components/Footer'
import AOS from 'aos'
import 'aos/dist/aos.css'

/* 
    ==========================
            Blog Page
    ==========================
*/
const Blog = () => {

    /* Start the scrolling animation library when the component is mounted */
    useEffect(() => {
        AOS.init()
    })

    /* Render the page */
    /* Subcomponents (placed in order):
        + Navigation bar
        + Body - Blog posts
        + Footer
    */
    
    return(
        <Fragment>
            <NavBar />
            <section className="container">
                <h1 data-aos="zoom-in" className="page-label border-bottom">Blog</h1>
                <div data-aos="fade-up" data-aos-once="true" className="feature-explanation">
                    <h2 className="links-not-working">Version 1 <span>This will be a link</span></h2>
                    <p>First version of the package already available on Github. This version includes rendering of the Joystick and logic that returns the value of the movement. Further features will be added on future versions</p>
                </div>

                <p className="note-comment">Note: Posts below work as an example and will be implemented in the future.</p>

                <div data-aos="fade-up" data-aos-once="true" className="feature-explanation">
                    <h2>Version 2</h2>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur eget augue eu ligula vehicula volutpat. Proin ac feugiat eros, et sollicitudin leo. </p>
                </div>

            </section>
            <Footer smallContent={true} />
        </Fragment>
    )
}

export default Blog