import React, { useEffect, useState } from 'react'
import Canvas from './Canvas'
import './Styles/Implementation.css'
import UnderlinedTitle from './UnderlinedTitle'

/* 
    ==========================
        Implementation Page 
    ==========================
*/

/* Renders an implementation examples of the Product with a short description on the side */

const Implementation = () => {

    /* Use state: position of the joystick, angle and state of rendering */
    const [position, setPosition] = useState({x: 0, y: 0})
    const [angle, setAngle] = useState(0)
    const [mount, setMount] = useState(true)


    /* Callback sent to the Joystick class, which will be called everytime the joystick renders */
    const handleChange = (data) => {

        if(mount){
            setPosition(data.movement)
            setAngle(data.angle)
        }     
    }

    /* Clean Up the state */
    useEffect(() => {
        setMount(true)

        return () => {
            setMount(false)
        }
    }, [])

    /* Return the component that's to be rendered. It uses different bootstrap classes for creating the grid */
    return(
        <section id="examples" className="site-demonstration container">
            <div className="canvas-and-data clearfix">
                <div className="try-it-title">
                    <UnderlinedTitle text="Try it!" />
                </div>
                <div className="demonstration-joystick-info">
                    <p className="documentation-paragraph"> The Joystick returns the value of movement in a scale of -1 to 1 for horizontal and vertical displacement, and the angle between both circles: </p>
                    <div className="demonstration-data">
                        <p><span>X: </span>{position.x}</p>
                        <p><span>Y: </span>{position.y}</p>
                        <p><span>Angle: </span>{angle}</p>
                    </div>
                    <p style={{ fontSize: "1rem"}} className="documentation-paragraph"> <span style={{color: "#ff5b5b"}} >Note:</span> Y axis values returned are inverted following the coordinate system employed by the CANVAS tag. See more <a style={{color: "#ff5b5b"}} href="https://www.w3schools.com/graphics/canvas_coordinates.asp" target="_blank">documentation</a></p>
                </div>
                <div className="demosntration-canvas-div">
                    <Canvas name="demonstration" onChange={handleChange} />
                </div>
            </div>
        </section>
    )
}

export default Implementation