import React , {Fragment, useEffect} from 'react'
import NavBar from '../Components/NavBar'
import Logo from '../Images/joystick.svg'
import GamingOriented from '../Images/gaming-oriented.svg'
import Responsive from '../Images/responsive.svg'
import Fast from '../Images/fast.svg'
import '../Components/Styles/Styles.css'
import {Link} from'react-router-dom'
import Implementation from '../Components/Implementation'
import UnderlinedTitle from '../Components/UnderlinedTitle'
import '@glidejs/glide/dist/css/glide.core.min.css'
import '@glidejs/glide/dist/css/glide.theme.min.css'
import Glide from '@glidejs/glide'
import Footer from '../Components/Footer'
import AOS from 'aos'
import 'aos/dist/aos.css'

/* 
 =========================
    Homepage Component
 =========================
*/


const Homepage = () => {

    /* When the page is loaded add Glide package's implementation */
    useEffect(() => {
        new Glide('.glide', {
            startAt: 0,
            perView: 3,
            breakpoints: {
                768: {
                    perView: 1
                }
            }
        }).mount()

        AOS.init()
    })

    /* Render homepage */
    /* Subcomponents (placed in order):
        + Navigation bar
        + Interactive Sample
        + Body
        + Footer
    */
    return (
        <Fragment>
            <header>
                <NavBar />
                <div className="jumbotron homepage-header">
                    <div data-aos="zoom-in" className="d-flex justify-content-center align-items-center logo-title">
                        <img src={Logo} alt="Logo of the package" />
                        <h1 style={{margin: "revert"}}>DragJoystick</h1>
                    </div>
                    <p className="text-center text-white">A dependency free JoyStick package for JS Games</p>
                    <Link type="button" to="/docs" className="btn btn-danger btn-sm get-started-btn">Get started </Link>
                </div>
            </header>
            <main>
                <Implementation />
            </main>
            <section className="background-features">
                <div className="site-features-container">
                    <UnderlinedTitle text="Main features" />
                    <div className="row site-features">
                        <div data-aos="fade-up" data-aos-once="true" className="col-md single-feature">
                            <img src={GamingOriented} alt="Play station controller" />
                            <h3>Gaming Oriented</h3>
                        </div>
                        <div data-aos="fade-up" data-aos-once="true" className="col-md single-feature">
                            <img src={Responsive} alt="Desktop and mobile" />
                            <h3>Mobile Support</h3>
                        </div>
                        <div data-aos="fade-up" data-aos-once="true" className="col-md single-feature">
                            <img src={Fast} alt="Speedometer"/>
                            <h3>Fast performance</h3>
                        </div>
                    </div>
                </div>
            </section>
            <section className="container site-projects">
                <UnderlinedTitle text="Project Examples" />
                <div className="glide project-examples-section">
                    <div className="glide__track" data-glide-el="track">
                        <ul className="glide__slides">
                        <li className="glide__slide">
                            <div className="project-examples">
                                <p>Should be displayed an image with a link to a videogame that employs this package</p>
                                <p className="text-center">Try to drag!</p>
                            </div>
                        </li>
                        <li className="glide__slide">
                            <div className="project-examples">
                                <p>Should be displayed an image with a link to a videogame that employs this package</p>
                                <p className="text-center">Try to drag!</p>
                            </div>
                        </li>
                        <li className="glide__slide">
                            <div className="project-examples">
                                <p>Should be displayed an image with a link to a videogame that employs this package</p>
                                <p className="text-center">Try to drag!</p>
                            </div>
                        </li>
                        </ul>
                    </div>
                    <div className="glide__arrows" data-glide-el="controls">
                        <button className="glide__arrow glide__arrow--left text-dark" data-glide-dir="<"><i className="fas fa-arrow-circle-left"></i></button>
                        <button className="glide__arrow glide__arrow--right text-dark" data-glide-dir=">"><i className="fas fa-arrow-circle-right"></i></button>
                    </div>
                </div>
            </section>
            <Footer />
        </Fragment>
    )
}
    

export default Homepage