import React from 'react'
import {Link} from 'react-router-dom'
import './Styles/Styles.css'
import Logo from '../Images/joystick.svg'

/* NavBar developed using Bootstrap */

/* Note: Link tags are used instead of <a> because the project uses react-dom-router making it a single-page application */

const NavBar  = () => (
    <div className="background-navbar">
        <nav className="navbar background-navbar navbar-expand-lg navbar-light">
            <Link className="navbar-brand text-white d-block d-lg-none" to="/"> <img src={Logo} alt="Logo of the package" width="50" height="50" /> DragJoystick</Link>
            <button className="navbar-toggler white-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse justify-content-around" id="navbarSupportedContent">
                <Link className="navbar-brand d-none d-lg-block text-light" to="/"> <img src={Logo} alt="Logo of the package" width="50" height="50" /> DragJoystick</Link>
                <div className="nav-item">
                    <Link className="nav-link text-white" to="/docs"> Docs </Link>
                </div>
                <div className="nav-item">
                    <Link className="nav-link text-white" to="/examples">Examples</Link>
                </div>
                <div className="nav-item">
                    <Link className="nav-link text-white" to="/blog">Blog</Link>
                </div>
            </div>
        </nav>
    </div> 
)
export default NavBar