import React, {Fragment, useEffect} from 'react'
import NavBar from '../Components/NavBar'
import Footer from '../Components/Footer'
import AOS from 'aos'
import 'aos/dist/aos.css'

/* 
    =================================
            Documentation Page
    =================================
*/

const Documentation = () =>{

    /* Start the scrolling animation library when the component is mounted */
    useEffect(() => {
        AOS.init()
    })

    /* Render documentation page */
    /* 
        + Navigation bar
        + Body - Features explanation and further links
        + Footer
    */
    return(
        <Fragment>
            <NavBar />
            <section className="site-documentation">
                <div className="row" style={{margin: 0}}>
                    <div className="col-md-8 site-features-documentation">
                        <h1 data-aos="zoom-in" data-aos-once="true" className="page-label border-bottom">Documentation - Introduction</h1>
                        <div data-aos-once="true" data-aos="fade-up" className="feature-explanation">
                            <h2>Gaming Oriented</h2>
                            <p>The package is specifically oriented for web game development. The API offers different functions that will allow you to get values corresponding to the movement of a player.</p>
                        </div>
                        <div data-aos-once="true" data-aos="fade-up" className="feature-explanation">
                            <h2>Mobile Friendly</h2>
                            <p>Offering support for every kind of device is the main goal of this project. It was originally intended to only work on mobile devices as they rely on touch screens to produce the movement. </p>
                        </div>
                        <div data-aos-once="true" data-aos="fade-up" className="feature-explanation">
                            <h2>Fast</h2>
                            <p>The package performs really well in different devices and has been optimized during the development. in this way, the modular structure provides compression therefore it weighs only ~13kb </p>
                        </div>
                        <div data-aos-once="true" data-aos="fade-up" className="feature-explanation">
                            <h2>Open Source</h2>
                            <p>This is an open-source package, which means that you can download the source code an adapt it to whatever your future game will need.</p>
                        </div>
                    </div>
                    <div className="col-md-4 links-documentation">
                        <h1 className="page-label useful-links">Useful Links</h1>
                        <div className="mx-auto">
                            <ul data-aos-once="true" data-aos="fade-up" className="list-unstyled">
                                <li>
                                    <p className="links-not-working">Installation <span>This will be a link</span></p>
                                </li>
                                <li>
                                    <p className="links-not-working">API <span>This will be a link</span></p>
                                </li>
                                <li>
                                    <p className="links-not-working">Source Code <i className="fab fa-github"></i> <span>This will be a link</span></p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </Fragment>
    )
    
}

export default Documentation