import React from 'react'
import {Link} from 'react-router-dom'

/* 
    ===========================
        Footer of the website 
    ===========================

    Developed mainly with boostrap
*/

/* Note: Link tags are used instead of <a> because the project uses react-dom-router making it a single-page application */

/* 
    The prop allows to know whether the page that renders the footer has enough content to leave
    naturally the footer at the bottom, or in case it doesn't then a class 'small-page-footer' will
    force it to be at the bottom
*/

const Footer = ({smallContent}) => (
    <footer className={"page-footer site-footer " + (smallContent ? 'small-page-footer': '')} >
        <div className="container">
            <div className="row text-white">
                <div className="col-md mx-auto footer-docs">
                    <Link to="/docs" className="docs-link">Docs</Link>
                    <ul className="list-unstyled">
                        <li>
                            <p className="links-not-working">Installation <span>This will be a link</span></p>
                        </li>
                        <li>
                            <p className="links-not-working">API <span>This will be a link</span></p>
                        </li>
                        <li>
                            <p className="links-not-working">Source Code <span>This will be a link</span></p>
                        </li>
                        <li>
                            <Link className="text-white" to="/examples">Examples</Link>
                        </li>
                    </ul>
                </div>
                <div className="col-md mx-auto text-center">
                    <h3 className="contributor">Contributor</h3>
                    <p><span>Email:</span> jorgelmh_01@hotmail.com</p>
                </div>
            </div>
            <div className="footer-copyright text-center copyright">
                <p>product icon made by <a style={{color: "#ff5b5b"}} href="https://www.flaticon.com/authors/smashicons" title="Smashicons" target="_blank">Smashicons</a> from <a href="https://www.flaticon.com/" style={{color: "#ff5b5b"}} target="_blank" title="Flaticon">www.flaticon.com</a></p>
                <p><i className="fas fa-copyright"></i> 2020 Copyright</p>
            </div>
        </div>
    </footer>
)

export default Footer